<template>
  <div>
    <div v-if="submission_message" class="d-flex justify-content-center w-100">
      <PostSubmission :message="message" />
    </div>
    <div v-else>
      <Test v-if="categories.length" class="mb-3" :candidate_details="candidate_details" :categories="categories" :is_mock_test="is_mock_test" :test_details="{ maximum_time: 0 }" />
    </div>
  </div>
</template>
<script>
  import Test from "./Test";
  import Mock from "@/js/ninjainterview/mock";
  import { bus } from "@/js/core/emit-bus";
  import PostSubmission from "./PostSubmission";

  export default {
    components: { Test, PostSubmission },
    props: ["candidate_details"],
    data() {
      return {
        categories: [],
        is_mock_test: true,
        submission_message: false,
        message: "<p>You have just submitted the response to the mock test. You can get back to take the actual test.</p><p>Good luck</p>",
      };
    },
    mounted() {
      this.getTestData();
      bus.$on("showSubmissinMessage", (type) => {
        if (type == "mocktest") this.submission_message = true;
      });
    },
    methods: {
      async getTestData() {
        let categories_data = Mock.categories;
        // Categories
        if (categories_data.length) {
          let obj = {};
          categories_data.forEach((question) => {
            if (!obj[question.category_id]) obj[question.category_id] = [];
            obj[question.category_id].push(question);
          });
          let categories = [];
          Object.keys(obj).map((key) => {
            let category = obj[key][0];
            category.questions = obj[key];
            categories.push(category);
          });
          this.categories = categories;
        }
      },
    },
  };
</script>
