<template>
  <div class="mt-md-3 mb-0">
    <b-form>
      <p v-html="test_details.test_instructions" class="bg-lighter border p-4 rounded test-instructions"></p>
      <!-- Open modal for mock test -->
      <QuestionCount v-bind="{ test_details, domain_id, test_id, categories, candidate_details }" @next="activeNextTab" />
      <input type="checkbox" class="cursor-pointer mr-2" v-model="is_checked" required />I confirm that I have understood all the instructions.
      <div class="border-top d-flex justify-content-end align-items-center mt-3 pt-3">
        <b-link v-b-modal.mocktest>Click here to take Mock Test</b-link>
        <b-button type="submit" variant="primary" size="sm" data-toggle="wizard" class="ml-3 px-4 py-2 mt-2 left" @click.prevent="checkCameraAndAudio" :disabled="!is_checked">
          Start Test
        </b-button>
      </div>
    </b-form>
    <b-modal
      size="xl"
      id="mocktest"
      title="Mock Test"
      header-class="border-bottom-0 mock-test-modal-header"
      footer-class="border-top-0 pt-2 d-flex justify-content-center"
      cancel-variant="secondary btn-sm px-3 mr-2 py-1"
      ok-variant="primary btn-sm px-4 py-1"
      body-class="py-3"
      hide-footer
    >
      <MockTest :candidate_details="candidate_details" />
    </b-modal>

    <GrantPermission :show_button="false" ref="grant_permission" :browser_type="browser_type" @ok="startTest" />
  </div>
</template>
<script>
  import PositionTest from "@/js/ninjainterview/position-tests";
  import MockTest from "./MockTest.vue";
  import GrantPermission from "./GrantPermission.vue";
  import QuestionCount from "./QuestionCount.vue";

  export default {
    props: { test_details: Object, domain_id: String, test_id: String, candidate_details: Object, browser_type: Object, categories: Array },
    components: { MockTest, QuestionCount, GrantPermission },
    data() {
      return {
        is_checked: false,
      };
    },
    methods: {
      // Start assessment
      async startTest() {
        PositionTest.params = `browser=${this.browser_type.browser}&OS=${this.browser_type.OS}&device_type=${this.browser_type.deviceType}`;
        let response = await PositionTest.addCandidateTest(this.domain_id, this.test_id, this.candidate_details.id);
        if (response.status) this.candidate_details.test_candidate_id = response.status;
        this.$emit("next", "layout", 2);
      },
      // Check camera and microphone. request media devices
      checkCameraAndAudio() {
        // Check assessment contains audio or video questions
        if (this.categories.some((category) => category.questions.some((question) => question.type == "Video Recording" || question.type == "Voice Recording"))) {
          // Check client has webcam and not given permission
          if (this.$refs.grant_permission.hasWebcam && !this.$refs.grant_permission.hasCamAccess) {
            this.$refs.grant_permission.requestMediaDeviceAccess();
            return;
          }
          // Check client has microphone and not given permission
          if (this.$refs.grant_permission.hasMicrophone && !this.$refs.grant_permission.hasMicAccess) {
            this.$refs.grant_permission.requestMediaDeviceAccess();
            return;
          }
        }
        this.startTest();
      },
    },
  };
</script>
<style>
  .test-instructions p {
    margin-bottom: 8px !important;
  }
  .test-instructions p br {
    display: block;
    content: "";
    height: 8px;
  }
</style>
